export default [
  {
    name: 'text',
    title: '文本组件',
    content: '新建的文本组件',
    parentStyle: {
      top: 0,
      left: 0
    },
    style: {
      top: 0,
      left: 0,
      width: false,
      height: false,
      fontSize: 14,
      color: '#666666',
      lineHeight: 19,
      textAlign: 'center',
      background: '',
      fontWeight: '',
      textDecoration: '',
      fontStyle: '',
      BoxShadow: {
        show: false,
        name: 'box-shadow',
        style: {
          level: 0,
          vertical: 0,
          radius: 5,
          extension: 3,
          color: '#666666',
          inset: false
        }
      },
      Border: {
        show: false,
        name: 'border',
        style: {
          width: 1,
          style: 'solid',
          color: '#666666'
        }
      }
    }
  },
  {
    name: 'image',
    title: '图片组件',
    content: {
      src: 'data:image/jpeg;base64,',
      title: '这才算东西'
    },
    parentStyle: {
      top: 30,
      left: 20,
      textAlign: 'center'
    },
    style: {
      height: 60,
      width: 50,
      left: 20,
      top: 30,
      borderRadius: 0,
      opacity: '1',
      Border: {
        name: 'border',
        show: false,
        style: {
          width: 1,
          style: 'solid',
          color: '#666666'
        }
      },
      BoxShadow: {
        show: false,
        name: 'box-shadow',
        style: {
          level: 0,
          vertical: 0,
          radius: 5,
          extension: 3,
          color: '#666666',
          inset: false
        }
      }
    }
  },
  {
    name: 'free',
    title: '自由面板组件',
    parentStyle: {
      height: 150,
      width: 320,
      background: '#ffffff',
      outline: {
        show: true,
        name: 'outline',
        style: {
          width: 1,
          style: 'dashed',
          color: '#dddddd'
        }
      }
    },
    child: []
  },
  {
    name: 'seckill',
    title: '秒杀组件',
    parentStyle: {
      marginTop: 8,
      opacity: '1',
      color: '',
      background: '#ffffff'
    },
    css: 'thrid-mode',
    style: {
      height: 250,
      marginBottom: 0,
      background: '#ffffff'
    },
    imgStyle: {
      height: 140,
      width: 140
    },
    titleStyle: {
      fontSize: 15,
      color: '#666666'
    },
    method: {
      service: false,
      integral: false,
      goodsData: 'ECommerce',
      goodsClassify: 'all'
    }
  }
]



